import React, {useState, useEffect} from "react";
import cookie from "react-cookies";
import axios from "axios";
import {getUrlData} from "../../util/getUrlData";

const AppointmentIndex = (props) => {

    const [loading, setLoading] = useState(true);

    const [typeList] = useState([
        {'id': 0, 'name': '', type: ["ALL"]},
        {'id': 1, 'name': '/', type: ["luckydraw"]},
        {'id': 2, 'name': '', type: ["product_try", "product_buy"]},
        {'id': 3, 'name': '', type: ["ambassador"]},
        {'id': 4, 'name': '', type: ["event"]},
    ])
    const [typeListBtn, setTypeListBtn] = useState(0)

    const [navList, setNavList] = useState([])
    const [navListBtn, setNavListBtn] = useState(0)

    const [appointmentIndexData, setAppointmentIndexData] = useState({
        booking_list: {
            id: '',  //  id
            title: '',   //  
            status: '',  //  
            type: '',    //    product_try  product_buy  event  ambassador  luckydraw 
            luckydraw_type: '',  //  0   
            booking_time: '',
        },
        prod_list: {
            thumbnail: '',   //  
            product_name: '',    //  
            price: '',   //  
            lack_stock: '',  //  
            product_code: '' //  
        }
    })

    useEffect(() => {
        switch (typeListBtn) {
            case 0:
                setNavList([
                    {id: 0, list: '', status: ["-2", "-1", "0", "1", "2", "3", "4"]},
                    {id: 1, list: '', status: ["0"]},
                    {id: 2, list: '', status: ["1", "4"]},
                    {id: 3, list: '', status: ["-1"]},
                    {id: 4, list: '', status: ["-2", "2", "3"]},
                    {id: 5, list: '', status: ["0"]},
                    {id: 6, list: '', status: ["1"]},
                    {id: 7, list: '', status: ["2", "4"]},
                    {id: 8, list: '', status: ["-1"]},
                ]);
                break;
            case 1:
                setNavList([
                    {id: 0, list: '', status: ["-1", "0", "1", "2", "4"]},
                    {id: 1, list: '', status: ["0"]},
                    {id: 2, list: '', status: ["1"]},
                    {id: 3, list: '', status: ["2", "4"]},
                    {id: 4, list: '', status: ["-1"]},
                ]);
                break;
            default:
                setNavList([
                    {id: 0, list: '', status: ["-2", "-1", "0", "1", "2", "3", "4"]},
                    {id: 1, list: '', status: ["0"]},
                    {id: 2, list: '', status: ["1", "4"]},
                    {id: 3, list: '', status: ["-1"]},
                    {id: 4, list: '', status: ["-2", "2", "3"]},
                ]);
        }
    }, [typeListBtn])


    useEffect(() => {
        if (loading) {
            axios.get("/booking/index?store_id=" + getUrlData('store_id')).then(
                (res) => {
                    let resData = res.data;
                    if (Number(resData.code) === 200) {
                        console.log(resData)
                        cookie.save('store_name', resData.data.store_info.store_name)

                        setAppointmentIndexData(resData.data)

                        setLoading(false)
                    }
                },
                (error) => {
                    console.log(error)
                }
            )

        }
    }, [loading])

    if (loading)
        return (<div>loading</div>)

    return (
        <div className="AppointmentIndex">
            <div className={'headers'}>
                <div className="store-name">
                    {cookie.load('store_name')}
                </div>
            </div>
            <div className={'appointment_type'}>
                <h5></h5>
                <ul>
                    {
                        typeList.map((item, index) => {
                            return <li key={index} onClick={() => {
                                setTypeListBtn(index);
                                setNavListBtn(0);
                            }} className={item.id === typeListBtn ? 'on' : ''}>{item.name}</li>
                        })
                    }
                </ul>
            </div>
            <div className={'appointment_type'}>
                <h5></h5>
                <ul>
                    {
                        navList.map((item, index) => {
                            return <li key={index} onClick={() => {
                                setNavListBtn(index)
                            }} className={item.id === navListBtn ? 'on' : ''}>{item.list}</li>
                        })
                    }
                </ul>
            </div>
            <div className={'appointment_content'}>
                {
                    appointmentIndexData.booking_list.map((item, index) => {
                        if (typeListBtn === 0) {
                            if (item.type !== "luckydraw") {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={
                                        item.status === '-2' || item.status === '-1' || item.status === '2' || item.status === '3' ?
                                            "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/appointment/details" + props.location.search + "&booking_id=" + item.id + "&type=" + item.type)
                                    }}>
                                        <p className={'appointment_list_type'}>{
                                            item.type === 'product_try' ?
                                                "" : item.type === 'product_buy' ?
                                                "" : item.type === 'event' ?
                                                    "" : item.type === 'ambassador' &&
                                                    ""
                                        }</p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>
                                            {item.booking_time}
                                            <span>
                                    {
                                        item.status === '-2' ?
                                            "" : item.status === '-1' ?
                                            "" : item.status === '0' ?
                                                "" : item.status === '1' ?
                                                    "" : item.status === '2' ?
                                                        "" : item.status === '3' ?
                                                            "" : item.status === '4' ?
                                                                "" : null
                                    }
                                    </span>
                                        </p>
                                    </div>
                            } else {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={item.status === '-1' ?
                                        "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/commodity/detailsLuckydraw" + props.location.search + "&booking_id=" + item.id)
                                    }}>
                                        <p className={'appointment_list_type'}>{
                                            item.luckydraw_type === 0 ?
                                                "" : ""
                                        }</p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>{item.booking_time}
                                            <span>
                                    {
                                        item.status === '-1' ?
                                            "" : item.status === '0' ?
                                            "" : item.status === '1' ?
                                                "" : item.status === '2' ?
                                                    "" : item.status === '4' ?
                                                        "" : null
                                    }
                                </span>
                                        </p>
                                    </div>
                            }
                        } else if (typeListBtn === 1) {
                            if (item.type === "luckydraw") {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={item.status === '-1' ?
                                        "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/commodity/detailsLuckydraw" + props.location.search + "&booking_id=" + item.id)
                                    }}>
                                        <p className={'appointment_list_type'}>{
                                            item.luckydraw_type === 0 ?
                                                "" : ""
                                        }</p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>{item.booking_time}
                                            <span>
                                    {
                                        item.status === '-1' ?
                                            "" : item.status === '0' ?
                                            "" : item.status === '1' ?
                                                "" : item.status === '2' ?
                                                    "" : item.status === '4' ?
                                                        "" : null
                                    }
                                </span>
                                        </p>
                                    </div>
                            }
                        } else if (typeListBtn === 2) {
                            if (item.type === "product_try" || item.type === "product_buy") {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={
                                        item.status === '-2' || item.status === '-1' || item.status === '2' || item.status === '3' ?
                                            "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/appointment/details" + props.location.search + "&booking_id=" + item.id + "&type=" + item.type)
                                    }}>
                                        <p className={'appointment_list_type'}>{
                                            item.type === 'product_try' ?
                                                "" : item.type === 'product_buy' ?
                                                "" : null
                                        }</p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>
                                            {item.booking_time}
                                            <span>
                                    {
                                        item.status === '-2' ?
                                            "" : item.status === '-1' ?
                                            "" : item.status === '0' ?
                                                "" : item.status === '1' ?
                                                    "" : item.status === '2' ?
                                                        "" : item.status === '3' ?
                                                            "" : item.status === '4' ?
                                                                "" : null
                                    }
                                    </span>
                                        </p>
                                    </div>
                            }

                        } else if (typeListBtn === 3) {
                            if (item.type === "ambassador") {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={
                                        item.status === '-2' || item.status === '-1' || item.status === '2' || item.status === '3' ?
                                            "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/appointment/details" + props.location.search + "&booking_id=" + item.id + "&type=" + item.type)
                                    }}>
                                        <p className={'appointment_list_type'}></p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>
                                            {item.booking_time}
                                            <span>
                                    {
                                        item.status === '-2' ?
                                            "" : item.status === '-1' ?
                                            "" : item.status === '0' ?
                                                "" : item.status === '1' ?
                                                    "" : item.status === '2' ?
                                                        "" : item.status === '3' ?
                                                            "" : item.status === '4' ?
                                                                "" : null
                                    }
                                    </span>
                                        </p>
                                    </div>
                            }
                        } else if (typeListBtn === 4) {
                            if (item.type === "event") {
                                if (navList[navListBtn].status.some(sond => {
                                    return sond === item.status
                                }))
                                    return <div className={
                                        item.status === '-2' || item.status === '-1' || item.status === '2' || item.status === '3' ?
                                            "appointment_list end" : "appointment_list"} key={index} onClick={() => {
                                        props.history.push("/appointment/details" + props.location.search + "&booking_id=" + item.id + "&type=" + item.type)
                                    }}>
                                        <p className={'appointment_list_type'}></p>
                                        <p className={'appointment_list_title'}>{item.title}</p>
                                        <p>
                                            {item.booking_time}
                                            <span>
                                    {
                                        item.status === '-2' ?
                                            "" : item.status === '-1' ?
                                            "" : item.status === '0' ?
                                                "" : item.status === '1' ?
                                                    "" : item.status === '2' ?
                                                        "" : item.status === '3' ?
                                                            "" : item.status === '4' ?
                                                                "" : null
                                    }
                                    </span>
                                        </p>
                                    </div>
                            }
                        }
                    })
                }
            </div>
            {
                appointmentIndexData.booking_list.length === 0 &&
                <div className={"appointment_resultList"}>
                    <p className="noHasList"></p>
                    <div className="resultHeader">
                        
                    </div>
                    {
                        appointmentIndexData.prod_list.map((item, index) => {
                            return <div className="row" key={index} onClick={() => {
                                props.history.push("/commodity/details" + props.location.search + "&product_code=" + item.product_code)
                            }}>
                                <div className="resultItems">
                                    <div className="imgGrid">
                                        <img alt={''} src={item.thumbnail}/>
                                        {
                                            item.lack_stock &&
                                            <span className="o_stock"></span>
                                        }
                                    </div>
                                    <div className="item_copy">
                                        <p className="item_name">{item.product_name}</p>
                                        <p className="item_price"> <span> {item.price}</span></p>
                                    </div>
                                </div>
                            </div>
                        })
                    }
                </div>
            }

        </div>
    )
}

export default AppointmentIndex;
